import * as React from 'react';
import { ItemKey, ItemKeys, ItemSlot, RenderToolbar, SlotType } from '@ali/4ever-component';
import TaskListButton, { TaskListButtonProps } from './components/next/taskListButton';
import UnorderedListButton, { UnorderedListButtonProps } from './components/next/unorderedListButton';
import OrderedListButton from './components/next/orderedListButton';

const buttons: Partial<Record<ItemKeys, React.ComponentType<TaskListButtonProps> | React.ComponentType<UnorderedListButtonProps>>> = {
  'list.ulist': UnorderedListButton,
  'list.olist': OrderedListButton
};

export default function createRenderToolbar(
  config: any,
): RenderToolbar {


  return (props, controller) => {

    const { buttonName, extraProps } = props;

    const Component = buttons[buttonName] as React.ComponentType<TaskListButtonProps | UnorderedListButtonProps>;
    if (Component) {
      return (
        <ItemSlot display={SlotType.splitButton}>
          <Component
            controller={controller}
            locale={config.locale}
            enableSticker={config.enableSticker}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    if (buttonName === ItemKey['list.tlist']) {
      return (
        <ItemSlot display={SlotType.iconButton}>
          <TaskListButton
            controller={controller}
            locale={config.locale}
            {...extraProps}
          />
        </ItemSlot>
      )
    }

    return null
  };
}
